import { computed } from "vue";

export function defaultProps(props) {
  const getHelpText = computed(() => {
    if (typeof props.helpText === "function") return props.helpText();
    return props.helpText;
  });

  const getAddonAttrs = computed(() => {
    let attrs = {};
    if (props.name?.length) attrs.name = props.name;
    if (getHelpText.value?.length) attrs["aria-description"] = getHelpText;
    return attrs;
  });

  const inputSize = computed(() =>  {
    return (
      props.size  || 'lg'
    );
  })

  return { getHelpText, getAddonAttrs, inputSize };
}
